<template>
  <div>
    <div>
      <div style="display: flex;justify-content: space-between">
        <div>
          <el-input :disabled="showAdvanceSearchView" placeholder="请输入员工名搜索员工..." prefix-icon="el-icon-search" style="width: 300px;
margin-right: 10px" v-model="keyword" @input="initEmps" @keydown.enter.native="initEmps" @clear="initEmps" clearable></el-input>
          <el-button :disabled="showAdvanceSearchView" icon="el-icon-search" type="primary" @click="initEmps">搜索</el-button>
          <el-button type="primary" @click="showAdvanceSearchView = !showAdvanceSearchView">
            <i :class="showAdvanceSearchView?'fa fa-angle-double-up':'fa fa-angle-double-down'" aria-hidden="true"></i>
            高级搜索</el-button>
        </div>
        <div>
          <!-- elementui中的属性的类型是函数的时候，当有参数的时候，不用手动指定参数。会自动传入，
          :before-upload="beforeUpload"  这里面就没有传入参数。
          -->
          <el-upload
              :show-file-list="false"
              :before-upload="beforeUpload"
              :on-success="onSuccess"
              :disabled="importDataDisable"
              :on-error="onError"
              style="display: inline-flex;margin-right: 8px"
              action="/employee/basic/import">
            <el-button :disabled="importDataDisable" type="success" :icon="importDataBtnIcon">
              {{importDataBtnText}}</el-button>
          </el-upload>

          <el-button type="success" @click="exportData">
            <i class="fa fa-level-down"></i>
            导出数据</el-button>
          <el-button type="success" icon="el-icon-plus" @click="showAddEmpView">
            添加员工</el-button>
        </div>
      </div>
<!--高级搜索框-->
      <transition name="slide-fade">
        <div v-show="showAdvanceSearchView" style="border: 1px solid #409eff ;border-radius: 5px;box-sizing: border-box;padding: 5px;
        margin: 10px 0px;">
          <el-row>
            <el-col :span="5" >
              政治面貌：
              <el-select v-model="searchValue.politicId" placeholder="政治面貌" size="mini" style="width: 130px">
                <el-option
                    v-for="item in politicsstatus"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                </el-option>
              </el-select>
            </el-col>

            <el-col :span="4">
              民族：
              <el-select v-model="searchValue.nationId" placeholder="民族" size="mini" style="width: 130px">
                <el-option
                    v-for="item in nations"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                </el-option>
              </el-select>
            </el-col>
            <el-col :span="4">
              职位：
              <el-select v-model="searchValue.posId" placeholder="职位" size="mini" style="width: 130px">
                <el-option
                    v-for="item in positions"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                </el-option>
              </el-select>
            </el-col>
            <el-col :span="4">
              职称：
              <el-select v-model="searchValue.jobLevelId" placeholder="职称" size="mini" style="width: 130px">
                <el-option
                    v-for="item in joblevels"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                </el-option>
              </el-select>
            </el-col>

            <el-col :span="7">
              聘用形式：
              <el-radio-group v-model="searchValue.engageForm">
                <el-radio label="劳动合同">劳动合同</el-radio>
                <el-radio label="劳务合同">劳务合同</el-radio>
              </el-radio-group>
            </el-col>
          </el-row>
          <el-row style="margin-top: 10px">
            <el-col :span="5">
              所属部门：
              <el-popover
                  placement="right"
                  title="请选择部门"
                  width="200"
                  trigger="manual"
                  v-model="popVisible2">
                <el-tree default-expand-all  :data="allDeps" :props="defaultProps" @node-click="searchViewHandleNodeClick"></el-tree>
                <div slot="reference" style="box-sizing:border-box; padding-left: 8px;align-items: center; width: 130px ; display: inline-flex;margin-left:3px;font-size: 13px ;border: 1px solid #dedede;height: 26px;border-radius: 5px;
                   cursor: pointer;" @click="showAdvanceDepView">{{inputDepName}}
                </div>
              </el-popover>
            </el-col>

            <el-col :span="10">
              入职日期：
              <el-date-picker
                  v-model="searchValue.beginDateScope"
                  size="mini"
                  type="daterange"
                  unlink-panels
                  value-format="yyyy-MM-dd"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期">
              </el-date-picker>
            </el-col>

            <el-col :span="5" :offset="2">
              <el-button>
                取消
              </el-button>
              <el-button type="primary" size="mini" icon="el-icon-search" @click="initEmps('advanced')">搜索</el-button>
            </el-col>
          </el-row>
        </div>
      </transition>
    </div>

    <div style="margin-top: 10px">
      <el-table
          :data="emps"
          stripe
          border
          v-loading="loading"
          style="width: 100%">

        <el-table-column
            type="selection"
            width="55">
        </el-table-column>

        <el-table-column
            prop="name"
            label="姓名"
            fixed
            align="left"
            width="90">
        </el-table-column>
        <el-table-column
            prop="gender"
            label="性别"
            fixed
            align="left"
            width="90">
        </el-table-column>
        <el-table-column
            prop="idCard"
            label="工号"
            align="left"
            width="85">
        </el-table-column>
        <el-table-column
            prop="birthday"
            width="95"
            align="left"
            label="出生日期">
        </el-table-column>
        <el-table-column
            prop="idCard"
            width="150"
            align="left"
            label="身份证号码">
        </el-table-column>
        <el-table-column
            prop="wedlock"
            width="70"
            label="婚姻状况">
        </el-table-column>

        <el-table-column
            prop="nation.name"
            width="50"
            label="名族">
        </el-table-column>

        <el-table-column
            prop="nativePlace"
            width="85"
            label="籍贯">
        </el-table-column>
        <el-table-column
            prop="politicsstatus.name"
            label="政治面貌">
        </el-table-column>
        <el-table-column
            prop="email"
            width="180"
            align="left"
            label="电子邮件">
        </el-table-column>
        <el-table-column
            prop="address"
            width="220"
            align="left"
            label="联系地址">
        </el-table-column>
        <el-table-column
            prop="department.name"
            width="100"
            align="left"
            label="所属部门">
        </el-table-column>
        <el-table-column
            prop="position.name"
            width="100"
            label="职位">
        </el-table-column>
        <el-table-column
            prop="jobLevel.name"
            width="100"
            align="left"
            label="职称">
        </el-table-column>
        <el-table-column
            prop="engageForm"
            width="100"
            align="left"
            label="聘用形式">
        </el-table-column>
        <el-table-column
            prop="specialty"
            width="150"
            align="left"
            label="专业">
        </el-table-column>
        <el-table-column
            prop="school"
            width="150"
            align="left"
            label="毕业院校">
        </el-table-column>
        <el-table-column
            prop="beginDate"
            width="100"
            align="left"
            label="入职日期">
        </el-table-column>
        <el-table-column
            prop="conversionTime"
            width="100"
            align="left"
            label="转正日期">
        </el-table-column>
        <el-table-column
            prop="beginContract"
            width="95"
            align="left"
            label="合同起始日期">
        </el-table-column>
        <el-table-column
            prop="endContract"
            width="95"
            align="left"
            label="合同截止日期">
        </el-table-column>
        <el-table-column
            prop="contractTerm"
            width="100"
            label="合同期限"
            align="left"
            >
          <template slot-scope="scope">
            <el-tag>{{scope.row.contractTerm}}</el-tag>年
          </template>
        </el-table-column>
        <el-table-column
            prop="tiptopDegree"
            label="最高学历">
        </el-table-column>
        <el-table-column
            fixed="right"
            width="220"
            label="操作"> size="mini"
          <template slot-scope="scope">
            <el-button @click="showEditView(scope.row)" style="padding: 3px" size="mini">编辑</el-button>
            <el-button style="padding: 3px" size="mini" type="primary">查看高级资料</el-button>
            <el-button style="padding: 3px" size="mini" type="danger" @click="deleteEmp(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--分页功能 -->
      <div style="display: flex;justify-content: flex-end">
        <el-pagination
            background
            @current-change="currentChange"
            @size-change="sizeChange"
            layout="sizes, prev, pager, next, jumper, ->, total, slot"
            :total="total">
        </el-pagination>
      </div>
    <!--添加员工对话框-->
    </div>
    <el-dialog
        :title="title"
        :visible.sync="dialogVisible"
        width="80%"
      >
      <div>
        <el-form
            :model="emp" :rules="rules" ref="empForm"
        >
          <el-row>

            <el-col :span="6">
              <el-form-item label="姓名：" prop="name">
                <el-input prefix-icon="el-icon-edit" placeholder="请输入员工姓名" size="mini" style="width: 150px" v-model="emp.name"></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="5">
              <el-form-item label="性别：" prop="gender">
                <el-radio-group v-model="emp.gender">
                  <el-radio label="男">男</el-radio>
                  <el-radio label="女">女</el-radio>
                </el-radio-group>
              </el-form-item>
            </el-col>

            <el-col :span="6">
              <el-form-item label="出生日期：" prop="birthday">
                <el-date-picker
                    v-model="emp.birthday"
                    type="date"
                    size="mini"
                    style="width: 150px"
                    value-format="yyyy-MM-dd"
                    placeholder="出生日期">
                </el-date-picker>
              </el-form-item>
            </el-col>

            <el-col :span="6">
              <el-form-item label="政治面貌：" prop="politicId">
                <el-select v-model="emp.politicId" placeholder="政治面貌" size="mini" style="width: 200px">
                  <el-option
                      v-for="item in politicsstatus"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row>
            <el-col :span="6">
              <el-form-item label="民族：" prop="nationId">
                <el-select v-model="emp.nationId" placeholder="民族" size="mini" style="width: 150px">
                  <el-option
                      v-for="item in nations"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="5">
              <el-form-item label="籍贯：" prop="nativePlace">
                <el-input prefix-icon="el-icon-edit" placeholder="请输入籍贯" size="mini" style="width: 120px" v-model="emp.nativePlace"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="电子邮箱：" prop="email">
                <el-input prefix-icon="el-icon-message" placeholder="请输电子邮箱" size="mini" style="width: 150px" v-model="emp.email"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="7">
              <el-form-item label="联系地址：" prop="address">
                <el-input prefix-icon="el-icon-edit" placeholder="请输入联系地址" size="mini" style="width: 200px" v-model="emp.address"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="6">
              <el-form-item label="职位：" prop="posId">
                <el-select v-model="emp.posId" placeholder="职位" size="mini" style="width: 150px">
                  <el-option
                      v-for="item in positions"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="5">
              <el-form-item label="职称：" prop="jobLevelId">
                <el-select v-model="emp.jobLevelId" placeholder="职称" size="mini" style="width: 150px">
                  <el-option
                      v-for="item in joblevels"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="所属部门：" prop="departmentId">
                <el-popover
                    placement="right"
                    title="请选择部门"
                    width="200"
                    trigger="manual"
                    v-model="popVisible">
                  <el-tree default-expand-all  :data="allDeps" :props="defaultProps" @node-click="handleNodeClick"></el-tree>
                  <div slot="reference" style="box-sizing:border-box; padding-left: 8px;align-items: center; width: 150px ; display: inline-flex;font-size: 13px ;border: 1px solid #dedede;height: 26px;border-radius: 5px;
                 cursor: pointer;" @click="showDepView">{{inputDepName}}
                  </div>
                </el-popover>

              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="电话号码：" prop="phone">
                <el-input prefix-icon="el-icon-phone" placeholder="请输入电话号码" size="mini" style="width: 150px" v-model="emp.phone"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row>
              <el-col :span="6">
                <el-form-item label="工号：" prop="workID">
                  <el-input disabled prefix-icon="el-icon-edit" placeholder="请输入员工工号" size="mini" style="width: 150px" v-model="emp.wordID"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="5">
                <el-form-item label="学历：" prop="tiptopDegree">
                  <el-select v-model="emp.tiptopDegree" placeholder="学历" size="mini" style="width: 150px">
                    <el-option
                        v-for="item in tiptopDegrees"
                        :key="item"
                        :label="item"
                        :value="item">
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="毕业院校：" prop="school">
                  <el-input prefix-icon="el-icon-edit" placeholder="请输入毕业院校" size="mini" style="width: 150px" v-model="emp.school"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="7">
                <el-form-item label="专业名称：" prop="specialty">
                  <el-input prefix-icon="el-icon-edit" placeholder="请输入专业名称" size="mini" style="width: 150px" v-model="emp.specialty"></el-input>
                </el-form-item>
              </el-col>
          </el-row>

          <el-row>
            <el-col :span="6">
              <el-form-item label="入职日期：" prop="beginDate">
                <el-date-picker
                    v-model="emp.beginDate"
                    type="date"
                    size="mini"
                    style="width: 130px"
                    value-format="yyyy-MM-dd"
                    placeholder="出生日期">
                </el-date-picker>
              </el-form-item>
            </el-col>
            <el-col :span="5">
              <el-form-item label="转正日期：" prop="conversionTime">
                <el-date-picker
                    v-model="emp.conversionTime"
                    type="date"
                    size="mini"
                    style="width: 130px"
                    value-format="yyyy-MM-dd"
                    placeholder="转正日期">
                </el-date-picker>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="合同起始日期：" prop="beginContract">
                <el-date-picker
                    v-model="emp.beginContract"
                    type="date"
                    size="mini"
                    style="width: 130px"
                    value-format="yyyy-MM-dd"
                    placeholder="合同起始日期">
                </el-date-picker>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="合同终止日期：" prop="endContract">
                <el-date-picker
                    v-model="emp.endContract"
                    type="date"
                    size="mini"
                    style="width: 150px"
                    value-format="yyyy-MM-dd"
                    placeholder="合同终止日期">
                </el-date-picker>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row>

            <el-col :span="7">
              <el-form-item label="身份证号码：" prop="idCard">
                <el-input prefix-icon="el-icon-edit" placeholder="请输入身份证号码" size="mini" style="width: 200px" v-model="emp.idCard"></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="8">
              <el-form-item label="聘用形式：" prop="engageForm">
                <el-radio-group v-model="emp.engageForm">
                  <el-radio label="劳动合同">劳动合同</el-radio>
                  <el-radio label="劳务合同">劳务合同</el-radio>
                </el-radio-group>
              </el-form-item>
            </el-col>

            <el-col :span="9">
              <el-form-item label="婚姻状况：" prop="wedlock">
                <el-radio-group v-model="emp.wedlock">
                  <el-radio label="已婚">已婚</el-radio>
                  <el-radio label="未婚">未婚</el-radio>
                </el-radio-group>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="doAddEmp">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "EmpBasic",
    data(){
      return {
        popVisible2:false,
        popAdvanceVisible:false,
        searchValue:{
          politicId:null,
          nationId:null,
          jobLevelId:null,
          posId:null,
          engageForm:null,
          departmentId:null,
          beginDateScope:null
        },
        showAdvanceSearchView:false,
        //控制上传的按钮是否能够使用，在上传的时候是不允许再上传的。
        importDataDisable:false,
        importDataBtnIcon:'el-icon-upload2',
        importDataBtnText:'导入数据',
        title:'添加员工',
        allDeps:[],
        rules: {
          name: [{required:true,message:'请输入用户名',trigger:'blur'}],
          gender: [{required:true,message:'请输入性别',trigger:'blur'}],
          politicId: [{required:true,message:'请输入政治面貌',trigger:'blur'}],
          birthday: [{required:true,message:'请输入生日',trigger:'blur'}],
          nationId: [{required:true,message:'请输入民族',trigger:'blur'}],
          email: [{required:true,message:'请输入电子邮箱',trigger:'blur'},{type:'email',message: '邮箱格式不正确',trigger: "blur"}],
          nativePlace: [{required:true,message:'请输入籍贯',trigger:'blur'}],
          address: [{required: true, message: '请输入员工地址', trigger: 'blur'}],
          posId: [{required:true,message:'请输入职位',trigger:'blur'}],
          jobLevelId: [{required:true,message:'请输入职称',trigger:'blur'}],
          departmentId: [{required:true,message:'请输入所属部门',trigger:'blur'}],
          phone: [{required:true,message:'请输入电话号码',trigger:'blur'}],
          tiptopDegree: [{required:true,message:'请输入学历',trigger:'blur'}],
          school: [{required:true,message:'请输入毕业院校',trigger:'blur'}],
          specialty: [{required:true,message:'请输入专业名称',trigger:'blur'}],
          beginDate: [{required:true,message:'请输入入职日期',trigger:'blur'}],
          conversionTime: [{required:true,message:'请输入转正日期',trigger:'blur'}],
          beginContract: [{required:true,message:'请输入合同起始日期',trigger:'blur'}],
          endContract: [{required:true,message:'请输入合同终止日期',trigger:'blur'}],
          idCard: [{required:true,message:'请输入身份证号码',trigger:'blur'},{
            pattern: /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/,
            message: '身份证号码格式不正确',
            trigger: 'blur'
          }],
          engageForm: [{required:true,message:'请输入聘用形式',trigger:'blur'}],
          wedlock: [{required:true,message:'请输入婚姻状况',trigger:'blur'}],
        },
        //定义三个数组用来接收从后端传来的下拉框数据。
        nations:[],
        joblevels:[],
        politicsstatus:[],
        positions:[],
        //学历一般不会变，直接在前端写。
        tiptopDegrees:['本科','大专','博士','硕士','高中','初中','小学','其他'],
        popVisible:false,
        inputDepName:'所属部门',
        options: [{
          value: '选项1',
          label: '黄金糕'
        }, {
          value: '选项2',
          label: '双皮奶'
        }, {
          value: '选项3',
          label: '蚵仔煎'
        }, {
          value: '选项4',
          label: '龙须面'
        }, {
          value: '选项5',
          label: '北京烤鸭'
        }],
        emps:[],
        loading:false,
        total:0,
        page:1,
        dialogVisible:false,
        size:10,
        keyword:'',
        emp:{
          name:'jieqiyue',
          gender:'男',
          birthday:'1998-12-12',
          idCard:'6161623211232221',
          wedlock:"未婚",
          nationId:1,
          nativePlace:"江西",
          politicId:13,
          email:'2207061933@qq.com',
          phone:'13838383838',
          address:"江西上饶",
          departmentId:5,
          jobLevelId:9,
          posId:29,
          engageForm:"劳务合同",
          tiptopDegree:"本科",
          specialty:"计算机科学与技术",
          school:"东华理工大学",
          beginDate:'2021-6-6',
          workState:'在职',
          workID:"00000099",
          contractTerm:2,
          conversionTime:"2021-7-8",
          notworkDate:null,
          beginContract:"2020-6-5",
          endContract:"2020-8-5",
          workAge:null
        },
        defaultProps: {
          children: 'children',
          label: 'name'
        }
      }
    },
    mounted() {
      this.initEmps();
      this.initData();
      this.initPositions();
    },
    methods:{
      searchViewHandleNodeClick(data){
        this.popVisible2 = !this.popVisible2;
        this.inputDepName = data.name;
        this.searchValue.departmentId = data.id;
      },
      onError(err, file, fileList){
        this.importDataDisable = false;
        this.importDataBtnIcon = 'el-icon-upload2';
        this.importDataBtnText = '导入数据';
      },
      onSuccess(response, file, fileList){
        this.importDataDisable = false;
        this.importDataBtnIcon = 'el-icon-upload2';
        this.importDataBtnText = '导入数据';
        this.initEmps();
      },
      beforeUpload(){
        this.importDataDisable = true;
        this.importDataBtnIcon = 'el-icon-loading';
        this.importDataBtnText = '正在导入';
      },
      exportData(){
        window.open('/employee/basic/export','_parent');
      },
      showEditView(data){
          this.initPositions();
          this.inputDepName = data.department.name;
          this.title = "更新员工";
          this.dialogVisible = true;
          this.emp = data;
      },
      emptyEmp() {
        this.emp = {
          name: "",
          gender: "",
          birthday: "",
          idCard: "",
          wedlock: "",
          nationId: 1,
          nativePlace: "",
          politicId: 13,
          email: "",
          phone: "",
          address: "",
          departmentId: null,
          jobLevelId: 9,
          posId: 29,
          engageForm: "",
          tiptopDegree: "",
          specialty: "",
          school: "",
          beginDate: "",
          workID: "",
          contractTerm: 2,
          conversionTime: "",
          notworkDate: null,
          beginContract: "",
          endContract: "",
          workAge: null
        }
        this.inputDepName = '';
      },
      deleteEmp(data){
        this.$confirm('此操作将永久删除 '+data.name+' 角色, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
         this.deleteRequest("/employee/basic/" + data.id).then(resp=>{
           this.initEmps();
         })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      doAddEmp(){
        if (this.emp.id){
          this.$refs['empForm'].validate(valid=>{
            if (valid){
              this.putRequest("/employee/basic/",this.emp).then(resp=>{
                if (resp){
                  this.dialogVisible = false;
                  this.initEmps();
                }
              })
            }
          })
        }else{
          this.$refs['empForm'].validate(valid=>{
            if (valid){
              console.log("准备更新的员工数据为：");
              console.log(this.emp);
              this.postRequest("/employee/basic/",this.emp).then(resp=>{
                if (resp){
                  this.dialogVisible = false;
                  this.initEmps();
                }
              })
            }
          })
        }

      },
      handleNodeClick(data){
        this.emp.departmentId = data.id;
        this.inputDepName = data.name;
        this.popVisible = !this.popVisible;
      },
      // 高级搜索和普通搜索的对话框都是这一个控制的。
      showDepView(){
        this.popVisible = !this.popVisible;
      },
      showAdvanceDepView(){
        this.popVisible2 = !this.popVisible2
      },
      getMaxWordID(){
        this.getRequest("/employee/basic/maxWordID").then(resp=>{
          if (resp){
            this.emp.workID = resp.obj;
          }
        })
      },
      //从服务端获取职位信息。这个会经常变动。所以不存在sessionStorage里面。
      initPositions(){
        this.getRequest("/employee/basic/positions").then(resp=>{
          if (resp){
            this.positions = resp;
          }
        })
      },
      // 对于民族，职称，等不变的数据，请求之后加载到浏览器内存中。
      initData(){
        //从后端获取nations数据
        if(!window.sessionStorage.getItem("nations")){
          this.getRequest("/employee/basic/nations").then(resp=>{
            if (resp){
              window.sessionStorage.setItem("nations",JSON.stringify(resp));
              this.nations = resp;
            }
          })
        }else{
          this.nations = JSON.parse(window.sessionStorage.getItem("nations"));
        }
        //从后端获取politicsstatus
        if(!window.sessionStorage.getItem("politicsstatus")){
          this.getRequest("/employee/basic/politicsstatus").then(resp=>{
            if (resp){
              window.sessionStorage.setItem("politicsstatus",JSON.stringify(resp));
              this.politicsstatus = resp;
            }
          })
        }else{
          this.politicsstatus = JSON.parse(window.sessionStorage.getItem("politicsstatus"));
        }
        //从后端获取joblevel
        if(!window.sessionStorage.getItem("joblevel")){
          this.getRequest("/employee/basic/joblevel").then(resp=>{
            if (resp){
              window.sessionStorage.setItem("joblevel",JSON.stringify(resp));
              this.joblevels = resp;
            }
          })
        }else{
          this.joblevels = JSON.parse(window.sessionStorage.getItem("joblevel"));
        }

        if(!window.sessionStorage.getItem("allDeps")){
          this.getRequest("/employee/basic/deps").then(resp=>{
            if (resp){
              window.sessionStorage.setItem("allDeps",JSON.stringify(resp));
              this.allDeps = resp;
            }
          })
        }else{
          this.allDeps = JSON.parse(window.sessionStorage.getItem("allDeps"));
        }
      },
      //添加员工的对话框。
      showAddEmpView(){
        this.emptyEmp();
        this.title = "添加员工";
        this.dialogVisible = true;
        this.getMaxWordID();
      },
      sizeChange(currentSize){
        this.size = currentSize;
        this.initEmps();
      },
      currentChange(currentPage){
        this.page = currentPage;
        this.initEmps();
      },
      initEmps(type){
        console.log(this.searchValue.beginDateScope);
        this.loading = true;
        let url = '/employee/basic/?page=' + this.page+'&size=' +this.size;

        if (type && type == 'advanced'){
          if (this.searchValue.politicId) {
            url += '&politicId=' + this.searchValue.politicId;
          }
          if (this.searchValue.nationId) {
            url += '&nationId=' + this.searchValue.nationId;
          }
          if (this.searchValue.jobLevelId) {
            url += '&jobLevelId=' + this.searchValue.jobLevelId;
          }
          if (this.searchValue.posId) {
            url += '&posId=' + this.searchValue.posId;
          }
          if (this.searchValue.engageForm) {
            url += '&engageForm=' + this.searchValue.engageForm;
          }
          if (this.searchValue.departmentId) {
            url += '&departmentId=' + this.searchValue.departmentId;
          }
          if (this.searchValue.beginDateScope) {
            url += '&beginDateScope=' + this.searchValue.beginDateScope;
          }
        }else{
          // 如果是高级搜索
          url += "&name="+this.keyword;
        }
        // 发送请求
        this.getRequest(url).then(resp=>{
          this.loading = false;
          if (resp){
            this.emps = resp.data;
            this.total = resp.total;
          }
        })
      }

    }
  }
</script>

<style>
  /* 可以设置不同的进入和离开动画 */
  /* 设置持续时间和动画函数 */
  .slide-fade-enter-active {
    transition: all .8s ease;
  }
  .slide-fade-leave-active {
    transition: all .4s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .slide-fade-enter, .slide-fade-leave-to
    /* .slide-fade-leave-active for below version 2.1.8 */ {
    transform: translateX(10px);
    opacity: 0;
  }
</style>